<!-- 基本面 -->
<template>
  <ul class="splite">
    <li class="con-item" v-for="(item,index) in ChartDataList" :key="index">
      <chart :chartData="item"></chart>
    </li>
  </ul>
</template>

<script>
import chart from "@/components/chart";

export default {
  data() {
    return {
      ChartDataList: [],
      titleList: [
        "DTD布伦特 3:2:1 裂解价差",
        "SG MOGAS 92# DUBAI 1:1 裂解价差",
        "SG GASOIL 0.5% DUBAI 1:1 裂解价差",
        "SG KERO DUBAI 1:1 裂解价差"
      ]
    };
  },
  components: {
    chart
  },
  methods: {
    setOption(result, index) {
      // result["title"]
      const txtTitle = this.titleList[index];
      const maxData = result["max"],
        minData = result["min"],
        avgData = result["avg"],
        curData = result["current"],
        lastData = result["last"];
      const maxMaxData = Math.max.apply(null, maxData),
        maxCurData = Math.max.apply(null, curData);
      const minMinData = Math.min.apply(null, minData),
        minCurData = Math.min.apply(null, curData);
      const yMax = Math.max(maxMaxData, maxCurData),
        yMin = Math.min(minMinData, minCurData);
      const diffMax = [];
      for (let i in maxData) {
        diffMax.push(maxData[i] - minData[i]);
      }
      let chartOption = {
        dataZoom: [
          {
            type: "inside",
            start: 0,
            end: 100
          }
        ],
        color: [
          "rgba(114, 119, 149, 1)",
          "rgba(226, 226, 226, 1)",
          "rgba(19, 180, 209, 1)",
          "rgba(245, 101, 118, 1)"
        ],
        legend: {
          type: "plain",
          textStyle: {
            color: "#fff",
            fontWeight: "400",
            fontSize: "30",
            lineHeight: "42",
            padding: [0, 80, 0, 10]
          },
          show: true,
          left:'95',
          bottom: "0",
          data: [
            {
              name: `${result.other.period}年数据区间`,
              // 强制设置图形为圆。
              icon: "roundRect"
            },
            {
              name: `${result.other.period}年平均数据`,
              // 强制设置图形为圆。
              icon: "roundRect"
            },
            {
              name: `${result.other.last_year}年数据`,
              // 强制设置图形为圆。
              icon: "roundRect"
            },
            {
              name: `${result.other.current_year}年数据`,
              // 强制设置图形为圆。
              icon: "roundRect"
            }
          ]
        },
        title: {
          text: txtTitle, //标题
          left: "40",
          top:'20',
          textStyle: {
            color: "#fff",
            fontSize: "60"
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            animation: false,
            label: {
              backgroundColor: "#ccc",
              fontSize: 28,
              borderColor: "#aaa",
              borderWidth: 1,
              shadowBlur: 0,
              shadowOffsetX: 0,
              shadowOffsetY: 0,
              textStyle: {
                color: "#222"
              }
            }
          },
          formatter: function(params) {
            if (params[3]) {
              return params[2].name + "<br />" + params[3].value;
            } else {
              return params[2].name;
            }
          }
        },
        grid: {
          //坐标系（）
          bottom: "140",
          left: "150",
          top: "200",
          right: "66"
        },
        xAxis: {
          show: true,
          type: "category",
          data: result.x_lable,
          offset: 25,
          axisLine: {
            show: false,
            lineStyle: {
              color: "#3b3e52",
              width:"1",
              type:'solid'
            }
          },
          axisLabel: {
            color: "#93959d",
            fontSize: 32
            
          },
          splitLine: {
            show: false
          },
          axisPointer: {
            type: "shadow"
          },

          axisTick: {
            //y轴刻度线
            show: false
          },
          boundaryGap: false
        },
        yAxis: {
          type: "value",
          name: "     USD/bbl.",
          offset: "20",
          nameGap: "40", //坐标轴名称与轴线之间的距离。
          nameLocation: "end",
          nameTextStyle: {
            color: "#93959d",
            fontSize: 28,
            align: "right",
            width:400,
            padding: [0, 85, 0, 0]
          },
          interval:5,
          minInterval:5,
          maxInterval:5,
          axisLabel: {
            color: "#93959d",
            fontSize: 28,
            formatter: function(params) {
                    return params.toFixed(2);
             }
          },
          axisLine: {
            show: false
          },
          splitLine: {
            //控制区域的分割线
            show: true,
            lineStyle: {
              color: "#3B3E52",
              width: 1,
              type: "solid"
            }
          },
          splitArea: {
            //控制区域颜色
            show: true,
            areaStyle: {
              color: ["transparent", "transparent"]
            }
          },
          axisTick: {
            //刻度线
            show: false
          },
          min: yMin,
          max: yMax
        },
        series: [
          {
            name: `${result.other.period}年数据区间`,
            type: "line",
            smooth:false,
            data: minData,
            symbol: "none",
            lineStyle: { normal: { color: "rgba(114,119,149,1)", opacity: 0 } },
            stack: "a",
            symbol: "none"
          },
          {
            name:  `${result.other.period}年数据区间`,
            type: "line",
            data: diffMax,
            smooth:false,
            symbol: "none",
            lineStyle: { normal: { color: "rgba(114,119,149,1)", opacity: 0 } },
            stack: "a",
            symbol: "none",
            areaStyle: { normal: { color: "rgba(114,119,149,0.28)" } }
          },

          {
            name:  `${result.other.period}年平均数据`,
            type: "line",
            smooth:false,
            data: avgData,
            itemStyle: { normal: { lineStyle: { width: 3, type: "solid" } } },
            symbol: "none",
            lineStyle: { normal: { width: 4, color: "rgba(226, 226, 226, 0.3)" } }
          },
          {
            name: `${result.other.last_year}年数据`,
            smooth:false,
            type: "line",
            data: lastData,
            itemStyle: { normal: { lineStyle: { width: 3, type: "solid" } } },
            symbol: "none",
            lineStyle: { normal: { width: 4, color: "rgba(19, 180, 209, 1)" } }
          },
          {
            name: `${result.other.current_year}年数据`,
            type: "line",
            smooth:false,
            data: curData,
            symbol: "none",
            symbolSize: 5,
            lineStyle: { normal: { width: 4, color: "#f1726d" } },
            itemStyle: {
              normal: {
                width: 3,
                color: "#f1726d"
              }
            }
          }
        ]
      };
      return chartOption;
    },
    setDtdbrent(i) {
      //dtd
      return this.$x.get(this.url.dtdbrent, {}).then(res => {
        let dtdOption = { option: this.setOption(res.data, i) };
        this.ChartDataList.push(dtdOption);
      });
    },
    setStodubaiMogas(i) {
      //mogas
      this.$x.get(this.url.stodubaiMogas, {}).then(res => {
        let mogasOption = { option: this.setOption(res.data, i) };
        this.ChartDataList.push(mogasOption);
      });
    },
    setStodubaiGasoil(i) {
      //gasoil
      this.$x.get(this.url.stodubaiGasoil, {}).then(res => {
        let gasoilOption = { option: this.setOption(res.data, i) };
        this.ChartDataList.push(gasoilOption);
      });
    },
    setStodubaiKero(i) {
      this.$x.get(this.url.stodubaiKero, {}).then(res => {
        let keroOption = { option: this.setOption(res.data, i) };
        this.ChartDataList.push(keroOption);
      });
    }
  },

  mounted() {
    // var funcs = [
    //   this.setDtdbrent,
    //   this.setStodubaiMogas,
    //   this.setStodubaiGasoil,
    //   this.setStodubaiKero
    // ];
    // (async () => {
    //   for (let i = 0; i < funcs.length; i++) {
    //     await funcs[i](i);
    //     // console.log("func" + (i + 1) + " well done");
    //   }
    //   // console.log("all well done");
    // })();
    this.setDtdbrent(0).then(h=>{
      this.setStodubaiMogas(1)
    }).then(s=>{
      this.setStodubaiGasoil(2)
    }).then(r=>{
      this.setStodubaiKero(3)
    })
  }
};
</script>

<style  scoped>
.splite {
  width: 100%;
  height: 1848px;
  box-sizing: border-box;
  padding-left: 60px;
  padding-top: 39px;
}
.splite li.con-item {
  float: left;
  width: 1830px;
  height: 940px;
  margin-right: 60px;
  color: #fff;
  background: rgba(45, 48, 67, 0.4);
  margin-bottom: 60px;
  padding: 40px 50px;
  box-sizing: border-box;
}
.splite li:nth-of-type(1),
.splite li:nth-of-type(2) {
  height: 838px;
}
.splite li.con-item:nth-of-type(2n) {
  margin-right: 0;
}
</style>