<template>
      <div :id="ContId" ref="chartRef"></div>
</template>

<script>
  import echarts from 'echarts'

  export default {
    name: "echartBlock",
    props: {
      chartData: {
        type: Object
      },
    },
    data() {
      return {
        ContId:'',
      }
    },
    methods: {
      setContId(){
        this.ContId = (Math.random()*10000000).toString(16).substr(0,4)+'-'+(new Date()).getTime()+'-'+Math.random().toString().substr(2,5);;
        this.$refs.chartRef.style.width = '100%'
        this.$refs.chartRef.style.height = '100%'
        const _this = this;
        return new Promise(function(resolve, reject){        //做一些异步操作
            resolve(_this.ContId);
        });
      },
    },
    mounted() {
      this.setContId().then(res=>{ 
        let myCharts = echarts.init(document.getElementById(res));
        myCharts.setOption(this.chartData.option);
      });
    },
  }
</script>

